import interfaces from '../../utils/interface.js'







export default {
	data() {
		return {
			mobile: '',
			verify: '',
			password: '',
			logining: true,
			isVerify: false, //显示获取验证码
			toVerify: false, //验证码输入
			toRes: false, //输入密码成功
		}
	},
	onLoad() {

	},
	methods: {

		inputChange(e) {
			const key = e.currentTarget.dataset.key; //监控输入
			this[key] = e.detail.value; //号码
			const {
				mobile
			} = this;

			var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (reg.test(mobile)) {
				this.isVerify = true;
			} else {
				this.isVerify = false;
			}

		},
		navBack() {
			setTimeout(() => {
				uni.navigateTo({
					url: './login'
				})
			}, 500)
		},
		huoqu() {
			//获取验证码
			let tell = this.mobile;
			this.request({
				url: interfaces.get_yan,
				method: "POST",
				data: {
					tell
				},
				success: ((res) => {
					this.logining = false;
					if (res.error_code == 0) {
						uni.showToast({
							icon: "success",
							title: "已发送,请注意查收",
						}, 500)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data,
						}, 500)
					}
				})
			});




















		},
		async toRegister() {
			// this.logining = true;
			const {
				mobile,
				verify,
				password
			} = this;

			if (!mobile) {
				uni.showToast({
					icon: "none",
					title: "请输入手机号"
				})
				return;
			} else if (!verify) {
				uni.showToast({
					icon: "none",
					title: "请输入验证码"
				})
				return;
			} else {
				//获取验证码	
				this.request({
					url: interfaces.zhuce, //验证 验证码
					method: "POST",
					data: {
						code: this.verify
					},
					success: ((res) => { //注册
						if (res.error_code === 0) {
							this.request({
								url: interfaces.set_password,
								method: "POST",
								data: {
									tell: this.mobile,
									password: this.password
								},
								success: ((res) => {
									if (res.error_code == 0) {
										uni.showToast({
											icon: "success",
											title: "注册成功!",
										}, 500)
										this.navBack(); //跳转登录
									}
								})
							});
						} else { //
							uni.showToast({
								icon: "none",
								title: "验证码错误",
							}, 500)
						}
					})
				});
				//h5



























			}
		}
	},

}
